import { OtuLoggerProvider } from './../otu-logger/otu-logger';
import { Injectable } from '@angular/core';
import { Firebase } from '@ionic-native/firebase';
import { Platform } from 'ionic-angular';
import { AngularFirestore } from 'angularfire2/firestore';

/*
  Generated class for the AlertProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AlertProvider {

  constructor(
                private firebase: Firebase
              , private afs: AngularFirestore
              , private platform: Platform
              , private logger: OtuLoggerProvider
              ) {}

  async getToken() {
    let token1;
    let token2;
    let token3;

    if (this.platform.is('android')) {
      alert('android');
      token1 = await this.firebase.getToken();
    }
    
    if (this.platform.is('ios')) {
      alert('ios');
      token2 = await this.firebase.getToken();
      await this.firebase.grantPermission();
    }

    if (this.platform.is('cordova')) {
      alert('cordova');
      token3 = await this.firebase.getToken();
      await this.firebase.grantPermission();
    }

    alert(this.platform.platforms());
    alert(typeof this.platform.platforms());
    this.saveToken(token1, token2, token3);
  }

  private saveToken(token1, token2, token3) {
    if (!token1) token1="Not received";
    if (!token2) token2="Not received";
    if (!token3) token3="Not received";

    const devicesRef = this.afs.collection('devices');
    const data = {
      userId: 'userId',
      android: token1,
      ios: token2,
      cordova: token3
    };
    this.logger.setToken(token1);
    this.logger.eventLog('getToken', data);

    alert('Writing in FS');

    let id = this.afs.createId();
    return devicesRef.doc(id).set(data).then(s => alert('Success in writing to FS'), f => {alert('Failed to write to FS'); alert(f);})
  }
  onNotifications() {
    return this.firebase.onNotificationOpen();
  }
}


